
import { mapState } from 'vuex'

export default {
  name: 'Rates',
  layout: 'fullscreen',
  data () {
    return {
      searchKey: '',
      rates: [],
      allRates: [],
      loaded: false,
      showVatInfo: true
    }
  },
  head () {
    return {
      title: `ðŸ“ž Jambo Talk - Rates (${this.creds.username})`
    }
  },
  computed: {
    ...mapState(['creds']),
    searchResults () {
      let tempRates = this.rates
      if (this.searchKey !== '' && this.searchKey) {
        tempRates = tempRates.filter(({ country, code, iso }) => {
          const value = `+${code} ${country} ${iso}`
          return value.toUpperCase()
            .includes(this.searchKey.toUpperCase())
        })
      }
      return tempRates.sort((a, b) => a.country.localeCompare(b.country))
    }
  },
  mounted () {
    this.$nuxt.$emit('safe_auth') // Get Server Token
    this.loadRates()
    this.$nuxt.$emit('full_title', 'Rates')
  },
  methods: {
    async loadRates () {
      try {
        const { data: rates } = await this.$axios.get('v0.1/rates')
        if (rates?.length) {
          this.rates = rates
          this.allRates = rates
        }
        this.loaded = true
      } catch (error) {
        console.log(error)
      }
    }
  }
}
